import { createZodDto } from 'nestjs-zod'
import { JSONSchema } from 'class-validator-jsonschema'
import { IsOptional } from 'class-validator'
import { AppSchema } from '@flowda-projects/flowda-types'
import { meta } from '@flowda-projects/flowda-engine'

const appCreateUpdateMeta = {
  extends: 'AppSchema',
  columns: [
    { name: 'displayName' },
    {
      name: 'description',
      column_type: 'textarea',
      validators: [
        {
          format: '^[\\w.%+-]+@[a-zA-Z0-9.-]+\\.[a-zA-Z]{2,}$',
          message: 'invalid email',
        },
      ],
    },
  ],
}

const AppCreateSchema = AppSchema.pick({
  displayName: true,
  description: true,
  tenantId: true,
}).extend({
  __meta: meta(appCreateUpdateMeta),
})

// todo: updateSchema  nestjs DTO 
// todo: createZodDto  floSchema
const AppUpdateSchema = AppSchema.pick({
  id: true,
  displayName: true,
  description: true,
})
  .partial()
  .extend({
    __meta: meta(appCreateUpdateMeta),
  })

export class AppUpdateDto extends createZodDto(AppUpdateSchema) {}

// todo:  zod  zod  share 
//  class validator 
export class AppCreateDto extends createZodDto(AppCreateSchema) {
  @IsOptional()
  @JSONSchema({
    description: 'test',
  })
  displayName: string
}
