import { SignupDto } from './dto/signup.dto'
import {
  AuthenticationError,
  IIdentity,
  IIdentityProviderService,
} from '@flowda-projects/flowda-types'
import * as bcrypt from 'bcrypt'
import { JwtService } from '../jwt/jwt.service'
import { IConfigService, IMailService } from '../../infra/index'
import { injectable } from 'inversify'
import type { JwtPayload } from 'jsonwebtoken'
import { Logger } from '@nestjs/common'
import { GenerateRecoveryCodeDto } from './dto/generateRecoveryCode.dto'
import keymachine from 'keymachine'
import { ResetPasswordDto } from './dto/resetPassword.dto'

export type TAuthOptions = {
  access_token_secret: string
  refresh_token_secret: string
  access_token_expire: number
  refresh_token_expire: number
}

/**
 *  nest module  inversify  auth service  base class
 * 3 child  bind
 *  inverisfy 
 * AppController -> @someDecorator('tenant') AuthService -> IdentityProvider<Tenant>
 *   bind(IdentityProvider).to(TenantIdentityProvider).when(request -> request.parent.target === 'tenant')
 *  child class  leaky abstraction
 * 
 *
 *
 * todo:  parent class  child class 
 *  composition identityProvider  set property
 *  base class 
 */
@injectable()
export abstract class AuthenticationService<T extends IIdentity> {
  private readonly logger = new Logger(AuthenticationService.name)
  protected options: TAuthOptions

  constructor(
    protected identityProvider: IIdentityProviderService<T>,
    protected jwt: JwtService,
    protected config: IConfigService,
    protected mailService: IMailService,
  ) {}

  protected setOptions(options: TAuthOptions) {
    this.options = options
  }

  async signup(
    dto: SignupDto,
    extraFields: Record<string, any> = {},
  ): Promise<TExcludedIdentity<T>> {
    const user = await this.identityProvider.find({
      name: dto.name,
    })
    if (user) {
      throw new AuthenticationError.AccountNameAlreadyExists()
    }

    let hashedPassword: string | null = null
    if (dto.password) {
      // todo: /
      hashedPassword = await bcrypt.hash(dto.password, 10)
    }

    // const { password, ...rest } = dto //  password
    const a = {
      ...dto, //  passwordidentity  identity 
      hashedPassword: hashedPassword,
      hashedRefreshToken: null,
      recoveryCode: null,
      recoveryToken: null,
      ...extraFields,
    }
    const newUser = await this.identityProvider.create(a)

    return excludedIdentity<T>(newUser)
  }

  async validateUser(
    nameField: 'email' | 'name',
    name: string,
    password?: string,
  ): Promise<TExcludedIdentityAndRefreshToken> {
    const user = await this.doValidateUser(nameField, name, password)
    return excludedIdentityAndRefreshToken(user)
  }

  async validateUserReturnTokens(
    nameField: string,
    name: string,
    password?: string,
  ): Promise<{
    at: string
    rt: string
    user: TExcludedIdentityAndRefreshToken
    expireAt: number
  }> {
    const user = await this.doValidateUser(nameField, name, password)
    const { at, exp } = this.jwt.generateAccessToken(user.id, {
      secret: this.options.access_token_secret,
      exp: this.options.access_token_expire,
    })
    return {
      at,
      rt: user.refreshToken,
      user: excludedIdentityAndRefreshToken(user),
      expireAt: exp,
    }
  }

  /**
   *  jwt  cookie  access token  web API 
   *  cookie
   * e.g response.setHeader('Set-Cookie', cookie)
   */
  getCookieWithJwtToken(userId: string) {
    const { at, rt } = this.getTokens(userId)
    return [
      `Authentication=${at}; HttpOnly; Path=/; Max-Age=${this.options.access_token_expire}`,
      `Refresh=${rt}; HttpOnly; Path=/; Max-Age=${this.options.refresh_token_expire}`,
    ]
  }

  getTokens(userId: string) {
    const { at } = this.jwt.generateAccessToken(userId, {
      secret: this.options.access_token_secret,
      exp: this.options.access_token_expire,
    })
    const { rt } = this.jwt.generateRefreshToken(userId, {
      secret: this.options.refresh_token_secret,
      exp: this.options.refresh_token_expire,
    })
    return { at, rt }
  }

  async logout(userId: string) {
    await this.logoutApi(userId)
    return ['Authentication=; HttpOnly; Path=/; Max-Age=0', 'Refresh=; HttpOnly; Path=/; Max-Age=0']
  }

  async getUser(userId: string): Promise<TExcludedIdentity> {
    const user = await this.identityProvider.find({ id: userId })
    if (!user) {
      throw new AuthenticationError.AccountNotFound()
    }
    return excludedIdentity(user)
  }

  async logoutApi(userId: string): Promise<void> {
    const user = await this.identityProvider.find({ id: userId })
    if (!user) {
      throw new AuthenticationError.AccountNotFound()
    }
    const data = {
      ...user,
      hashedRefreshToken: null,
    }
    await this.identityProvider.update(data)
  }

  getAccessTokenSecret() {
    return this.options.access_token_secret
  }

  private async doValidateUser(
    nameField: string,
    name: string,
    password?: string,
  ): Promise<IIdentity & { refreshToken: string }> {
    const user = await this.identityProvider.find({
      [nameField]: name,
    })
    if (!user) {
      throw new AuthenticationError.UserNamePasswordMismatch()
    }

    // 
    if (password != null) {
      if (!user.hashedPassword) {
        throw new AuthenticationError.NotInitPassword()
      }

      const match = await bcrypt.compare(password, user.hashedPassword)
      if (!match) {
        throw new AuthenticationError.UserNamePasswordMismatch()
      }
    }

    const { rt } = this.jwt.generateRefreshToken(user.id, {
      secret: this.options.refresh_token_secret,
      exp: this.options.refresh_token_expire,
    })
    const hash = await bcrypt.hash(rt, 10)
    user.hashedRefreshToken = hash
    const updatedUser = await this.identityProvider.update(user)
    // db  refresh token refresh token  client
    return {
      ...user,
      refreshToken: rt,
    }
  }

  async refreshToken(
    rt: string,
  ): Promise<{ at: string; expireAt: number; user: TExcludedIdentity<T> }> {
    let decodedToken: JwtPayload
    try {
      decodedToken = this.jwt.verifyRefreshToken(rt, {
        secret: this.options.refresh_token_secret,
      })
    } catch (error) {
      throw new AuthenticationError.InvalidToken()
    }
    const uid = decodedToken.uid
    const user = await this.identityProvider.find({ id: uid })
    if (!user) {
      this.logger.error('cannot find uid')
      throw new AuthenticationError.InvalidToken()
    }
    if (user.hashedRefreshToken == null) {
      throw new AuthenticationError.NullRefreshToken()
    }
    const match = await bcrypt.compare(rt, user.hashedRefreshToken)
    if (!match) {
      throw new AuthenticationError.InvalidToken()
    }
    const { at, exp } = this.jwt.generateAccessToken(user.id, {
      secret: this.options.access_token_secret,
      exp: this.options.access_token_expire,
    })
    return { at, expireAt: exp, user: excludedIdentity(user) }
  }

  async generateRecoveryCode(dto: GenerateRecoveryCodeDto) {
    const user = await this.identityProvider.find({
      appId: dto.appId,
      email: dto.email,
    })
    if (!user) {
      throw new AuthenticationError.AccountNotFound()
    }

    const recoveryToken = this.jwt.generateRecoveryToken(dto.email, {
      secret: this.options.access_token_secret,
    })
    // token  code 
    const recoveryCode = this.generateRandomUppercaseLetter()
    user.recoveryToken = recoveryToken
    user.recoveryCode = recoveryCode
    await this.identityProvider.update(user)
    await this.mailService.legacySendEmail(dto.email, 'sdk customer recovery code', recoveryCode)
    return {
      recoveryCode,
    }
  }

  async resetPassword(dto: ResetPasswordDto): Promise<TExcludedIdentity<T>> {
    const user = await this.identityProvider.find({
      appId: dto.appId,
      recoveryCode: dto.recoveryCode,
    })
    if (!user) {
      throw new AuthenticationError.InvalidRecoveryCode()
    }
    const recoveryToken = user.recoveryToken
    if (recoveryToken == null) {
      throw new AuthenticationError.InvalidRecoveryCode()
    }
    let decodedToken
    try {
      decodedToken = this.jwt.verifyRecoveryToken(recoveryToken, {
        secret: this.options.access_token_secret,
      })
    } catch (error) {
      throw new AuthenticationError.InvalidRecoveryCode()
    }
    if (!decodedToken.verificationToken) {
      throw new AuthenticationError.InvalidRecoveryCode()
    }

    const hash = await bcrypt.hash(dto.password, 10)
    user.hashedPassword = hash
    user.hashedRefreshToken = null
    user.recoveryCode = null
    user.recoveryToken = null
    await this.identityProvider.update(user)
    return excludedIdentity(user)
  }

  generateRandomUppercaseLetter(len = 6) {
    return keymachine({
      possibility: 'ABCDEFGHIJKLMNOPQRSTUVWXYZ',
      length: len,
      case: 'upper',
    })
  }
}

export function exclude<Clazz, Key extends keyof Clazz>(
  clazz: Clazz,
  keys: Key[],
): Omit<Clazz, Key> {
  for (const key of keys) {
    delete clazz[key]
  }
  return clazz
}

export function excludedIdentityAndRefreshToken(user: IIdentity & { refreshToken: string }) {
  return exclude(user, [
    'hashedPassword',
    'refreshToken',
    'hashedRefreshToken',
    'recoveryCode',
    'recoveryToken',
  ])
}

export type TExcludedIdentityAndRefreshToken = Omit<
  IIdentity,
  'hashedPassword' | 'refreshToken' | 'hashedRefreshToken' | 'recoveryCode' | 'recoveryToken'
>

export function excludedIdentity<T extends IIdentity = IIdentity>(user: T) {
  return exclude(user, ['hashedPassword', 'hashedRefreshToken', 'recoveryCode', 'recoveryToken'])
}

export type TExcludedIdentity<T extends IIdentity = IIdentity> = Omit<
  T,
  'hashedPassword' | 'hashedRefreshToken' | 'recoveryCode' | 'recoveryToken'
>
